.wrapper {
  position: absolute;
  bottom: 20vh;
  width: 100%;
}

.list {
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container_li {
  margin: 0 20px;
  display: flex;
}

.item_number {
  background-color: #e6e6fa;
  border: 1px solid #000000;
  width: 40px;
  height: inherit;
  margin: 0;
  text-align: center;
  cursor: pointer;
}
